.tool-info-sheet {
	max-width: 1000px;
	margin: 0 auto;

	.tool-meta-data {
		@include screen-size(small) {
			margin-bottom: 40px;
		}	

		@include screen-size(medium-large) {
			padding-right: 90px;
			width: 35%;
			box-sizing: border-box;
			float: left;
		}

		position: -webkit-sticky;
		position: sticky;
		top: 50px;

		.tool-tags {
			margin-bottom: 15px;
		}
	}

	.tool-description {

		@include screen-size(medium-large) {
			width: 65%;
			float: left;
		}

		h1 {
			margin: 0 0 0.5em 0;
		}

		.tool-summary {
			font-weight: bold;
		}

		.tool-body-text {
			margin-bottom: 40px;
		}
	}
}