$cover-slide-backgound: none !default;

.training-card {
	background: white;
	padding: 60px;
	border-radius: $border-radii;
    box-shadow: $default-box-shadow;
    @include clear;
	margin-bottom: 20px;
	position: relative;

	.card-hero {
		margin-bottom: 20px;
		
		img {
			max-height: 400px;
			width: 100%;
			object-fit: cover;
		}
	}

	.training-card-section {
		padding: 0;

		@media print {
			padding: 0;
			border: none;
		}

		.card-illustrations {
			float: right;
			width: 30%;
			margin: 5px 0 40px 40px;

			.illustration {
				margin-bottom: 15px;
			}
		}

		.card-subtitle {
			margin-top: 0;
			margin-bottom: 15px;
			padding: 0;
			font-size: 25px;
		}

		.card-title {
			box-sizing: border-box;
			// height: 219px;
			font-weight: normal;
			overflow: hidden;
			padding: 0;
			margin-top: -200px;
			padding-top: 200px;
			margin-bottom: 30px;
			clear: both;


			.card-title-number {
				display: inline-block;
				background: rgba(black, 0.1);
				padding: 0 12px;
				line-height: 20px;
				height: 20px;
				font-size: 12px;
				border-radius: 0.7 * $border-radii;
				position: absolute;
				top: 18px;
				right: 18px;
				color: rgba(black, 0.55);
				font-family: monospace;		
				font-weight: bold;	
				letter-spacing: -1.5px;	
			}

			.card-title-label {
				display: block;
				font-stretch: condensed;
				font-size: 38px;
				line-height: 1.15;
			}
		}
	}
}